@import url(http://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.Button {
  background-color: white;
  font: inherit;
  border: 1px solid blue;
  padding: 8px;
}

.link {
  color: #4d4d4f;
  font-weight: bolder;
  width: 100%;
  text-align: center ;
  position: relative;
  padding: 5%;
}

.link:hover {
  text-decoration: none;
  color: red;
}

.navBar {
  width: 100%;
  text-align: center;
  padding: 10px;
  background-color: #f2f2f2;
  height: 70px;
  position: relative;
  overflow: hidden;
  padding-top: 20px;
  
}

.homeTitle {
  width: 100%;
  font-size: 80px;
  text-align: center;
  color: #4d4d4f;
  text-decoration: underline;
}

.homeSubTitle {
  width: 100%;
  text-align: center;
  color: #4d4d4f;
  
}


.formTitle {
  text-decoration: underline;
  width: 100%;
  color: #4d4d4f
}

.border {
  border: black;
  border-radius: 10px;
  border-top: 30px solid;
  padding: 20px;
  border-width: 20%;
  background-color: #f2f2f2;
}

.cardText {
  color: #4d4d4f;
}

.shoeInfoHolder{
  width: 100%;
  max-width: 100%;
  margin-left: 10%;
}

.searchMessage {
  padding-left: 10%;
}

.searchBar {
  width: 100%;
 margin-left: 20%;
}

input,
label {
  margin: 0.4rem 0;
}

.shoeImg {
  height: 250px;
  width: 445px;
}

.shoeName {
  text-decoration: underline;
}

.shoeInfoHolder{
  width: 100%;
  max-width: 100%;
  margin-left: 10%;
}

.shoeInfo {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 100%;
  max-width: 100%;
  float: left;
  display: inline;
  width: 49%;
  padding: 2px 16px;
  margin: 10px;
  padding-left: 10px;
  border: black;
  border-radius: 30px;
}

.cardText {
  color: #4d4d4f;
}

.pageText {
  text-align: center;
}


.fa-heart {
  color: #4d4d4f;
 
}

.fa-heart:hover {
  color: red;
  cursor: pointer
}

.btncontainer {
  margin-left: 50%;
  position: relative;
  bottom: 35px
}


.randomShoe {
    margin: 5% 5% 5% 5%;
    position: relative;
}

.grid {
    min-height: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    display: flex;
}

.randomShoeTitle {
    color:#b7b7b8;
    margin-top: 2.5%;
}

.randomButton {
    padding: 20px 32px;
    margin-top: 2.5%;
}

.randomShoeName{
    text-align: center;
}

.randomShoeContainer{ 
    padding: 20px;
    width: 100%;
    margin-left: 50%;
    position: relative;
}

.randomShoeInfoTitle {
    text-decoration: underline;
    text-align: center;
}

.randomShoeText {
    text-align: center;
}


.carousel .slide .legend {
    font-size: 25px;
    background: #f2f2f2;
    color: #4d4d4f;
    opacity: 0;
}

.carousel .slide {
  background: white;
}

.carousel .slide img {
    width: 40%
}

.carousel-container {
    position: relative;
}

.shoe-name{
    color: #4d4d4f;
    position: relative;
    margin-left: 20%;
    margin-right: 20%;
    text-align: center;
    border: solid #4d4d4f;
    font-weight: bold;
    border-radius: 25px;
}


.latestReleasesTitle {
    color:#b7b7b8 ;
} 

.carousel .carousel-status {
    text-shadow: unset;
}


.sneakerNewsTitle {
    color: #b7b7b8;
    margin-bottom: 2.5%;
}

.blockquote {
    color: #4d4d4f;
}

.card-title {
    color: #4d4d4f; 
}

.card-text {
    color:#4d4d4f;
}
.footer {
    overflow: hidden;
    background-color: #f2f2f2;
    position: relative;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    overflow: hidden;
    height: 17%;
    margin: 50px 0 0;
}
 
.icons{
	margin-top: 25px;
    text-align: center;
}

.icons a{
	display: inline-block;
	width: 35px;
	height: 35px;
	cursor: pointer;
	background-color:  #4d4d4f;
	border-radius: 2px;
    font-size: 20px;
	color: #f2f2f2;
	text-align: center;
	line-height: 35px;
	margin-right: 10px;
	margin-bottom: 5px; 
}

.name {
    text-align: center;
    color: #4d4d4f;
    font-size: 10px;
    line-height: 1.8rem
}


